<script setup>
import { ref, onMounted, onUnmounted } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
import { useUserStore } from "@/store/SignUp";
import { storeToRefs } from "pinia";
import "/src/assets/script/Main.js"; //    

// Vue Router  User Store 
const router = useRouter();
const userStore = useUserStore();
const { isLoggedIn, isAdmin } = storeToRefs(userStore);

//   
const currentPage = ref(1);
const totalPages = ref(1);
const posts = ref([]);

//   
const isMenuOpen = ref(false);

//   
const toggleMenu = () => {
  isMenuOpen.value = !isMenuOpen.value;
};

//   
const fetchPosts = async () => {
  try {
    const response = await axios.get("/posts", {
      params: { page: currentPage.value },
    });
    posts.value = response.data.posts;
    totalPages.value = response.data.totalPages;
  } catch (error) {
    console.error("Error fetching posts:", error);
  }
};

//  
const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
    fetchPosts();
  }
};

const nextPage = () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++;
    fetchPosts();
  }
};

const setPage = (page) => {
  currentPage.value = page;
  fetchPosts();
};

//     
const handleClick = async () => {
  if (isLoggedIn.value) {
    if (isAdmin.value) {
      router.push({ name: "PrModRecruitmentNotice" });
    } else {
      alert("   .");
    }
  } else {
    alert(" .");
    await handleLogin();
    router.push({ name: "Login" });
  }
};

const handleLogin = async () => {
  try {
    const response = await axios.post("/user/login", {
      username: userStore.username,
      password: userStore.password,
    });

    if (response.data.message) {
      userStore.isLoggedIn = true;
      userStore.isAdmin = response.data.grade === 1;

      //      
      if (userStore.isLoggedIn) {
        router.push({ name: "MainHome" });
      } else {
        router.push({ name: "Home" });
      }
    } else {
      console.error("Login failed");
      userStore.isLoggedIn = false;
    }
  } catch (error) {
    console.error("Error logging in user:", error);
    userStore.isLoggedIn = false;
  }
};

const logout = () => {
  userStore.isLoggedIn = false;
  userStore.isAdmin = false;
  userStore.$reset();
  router.push({ name: "Home" });
};

//         
const showBackToTop = ref(false);

const scrollToTop = () => {
  window.scrollTo({ top: 0, behavior: "smooth" });
};

const handleScroll = () => {
  showBackToTop.value = window.scrollY > 300; // 300px     
};

//     
onMounted(() => {
  fetchPosts(); //  
  window.addEventListener("scroll", handleScroll);
});

//     
onUnmounted(() => {
  window.removeEventListener("scroll", handleScroll);
});
</script>

